<template>
    <Transition name="scale">
        <div class="modal" @click="closeModal">
            <div class="modal-item" @click.stop="">
                <h3 class="modal-item__title">Отправить картинку</h3>
                <label class="modal-item__input">
                    <span>URL</span>
                    <input type="" placeholder="URL" v-model="message.title">
                </label>
                <label class="modal-item__input">
                    <span>Комментарий</span>
                    <input type="" placeholder="Комментарий" v-model="message.desc">
                </label>
                <div class="modal-item__btns">
                    <button class="modal-btn btn-cancel" @click="closeModal">ОТМЕНА</button>
                    <button class="modal-btn btn-sent" @click="addMessage">ОТПРАВИТЬ</button>
                </div>
            </div>
        </div>
    </Transition>
</template>

<script>
export default {
    data(){
        return {
            message: {
            id: 1,
            title: '',
            desc: '',

          }  
        }
    },
    methods: {
        closeModal(){
            this.$emit('closeModal', false)
            this.message.title = ''
            this.message.desc = ''
        },
    addMessage() {
      if (this.message.desc != "") {
        const message = {
          content:  this.message.desc,
          time: `${new Date().getHours()}: ${new Date().getMinutes()}`
        };
        console.log(message);
        this.$emit("addMessage", message);
        this.message.desc = ''
      }
    },
    }
}
</script>