<template>
  <section class="sentmessage">
    <div class="sentmessage-content">
      <input
        type="text"
        placeholder="Написать сообщение..."
        v-model="messageText"
      />

      <button
        class="sentmessage-content__sent"
        @click="openModal"
        v-if="messageText.length == 0"
      >
        <fa icon="fa-solid fa-camera" />
      </button>
      <button class="sentmessage-content__sent" v-else>
        <fa icon="fa-solid fa-paper-plane" @click="addMessage" />
      </button>
    </div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      messageText: "",
      id: 1,
      personTwo: "Jenya",
    };
  },
  methods: {
    openModal() {
      this.$emit("openModal", true);
    },
    onEnter() {
      window.addEventListener("keypress", (e) => {
        if (e.key === "Enter") {
          this.addMessage();
        }
      });
    },
    addMessage() {
      if (this.messageText != "") {
        const message = {
          content: this.messageText,
          id: this.id++,
          name: this.personTwo,
          time: `${new Date().getHours()}: ${new Date().getMinutes()}`,
        };
        console.log(message.time);
        this.$emit("addMessage", message);
        this.messageText = "";
      }
    },
  },
  created() {
    this.onEnter();
  },
};
</script>
