<template>
  <Modal v-show="modalOpenOrClose" @closeModal="closeModal" />
  <div class="container">
    <div class="container-content">
      <NavbarUser1 />
      <ContentUser1 userId="Alex" :messageList="messageList" />
      <SentMessageUser1
        :userId="1"
        @openModal="openModal"
        @addMessage="addMessage"
      />
    </div>

    <div class="container-content">
      <NavbarUser2 />
      <ContentUser2 userId="Jenya" :messageList="messageList" />
      <SentMessageUser2
        :userId="2"
        @openModal="openModal"
        @addMessage="addMessage"
      />
    </div>
  </div>
</template>
 
 <script>
import NavbarUser1 from "@/components/NavbarUser1.vue";
import ContentUser1 from "@/components/ContentUser1.vue";
import ContentUser2 from "@/components/ContentUser2.vue";
import SentMessageUser1 from "@/components/SentMessageUser1.vue";
import SentMessageUser2 from "@/components/SentMessageUser2.vue";
import NavbarUser2 from "@/components/NavbarUser2.vue";
import Modal from "@/components/Modal.vue";
export default {
  components: {
    NavbarUser1,
    ContentUser1,
    ContentUser2,
    SentMessageUser1,
    SentMessageUser2,
    NavbarUser2,
    Modal,
  },
  data() {
    return {
      modalOpenOrClose: false,
       messageList: [],
    };
  },
  methods: {
    openModal(bool) {
      this.modalOpenOrClose = bool;
    },
    closeModal(bool) {
      this.modalOpenOrClose = bool;
    },
    addMessage(message) {
      this.messageList.push(message);
      
    },
      getLocalList() {
        const localList = localStorage.list;
        if (localList) {
          this.messageList = JSON.parse(localList);
        }
      },
    },
    created() {
      this.getLocalList();
    },
    watch: {
      messageList: {
        handler(updateList) {
          localStorage.list = JSON.stringify(this.messageList);
        },
        deep: true,
      },
  },
};
</script>
 
 