<template>
  <div class="message">
    <div
      class="message-user"
      :class="{ send: message.name == userId, get: message.name != userId }"
    >
      <!-- <img :src="message.url" alt="" v-show="message.url.length > 0" /> -->
      <p>{{ message.content }}</p>
      <span>{{ message.time }}</span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    message: Object,
    userId: String,
  },
};
</script>
